@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @include breakpoint(m) {
    padding: 25px 0;
  }

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include breakpoint(m) {
      flex-direction: column;
      gap: 15px;
    }
  }

  &__logo {
    display: flex;
    min-width: 250px;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}
